import { Component, OnInit, OnDestroy } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import { Subscription } from 'rxjs';

import { AnewsService } from '../services/anews.service';
import { NouvellesModel } from '../modeles/nouvelles.modele';
import { NewsPipe } from './news.pipe';
import { ConnexionService } from '../services/connexion.service';

@Component({
  selector: 'app-an-edition',
  templateUrl: './an-edition.component.html',
  styleUrls: [
    './an-edition.component.css'
  ]
})
export class AnEditionComponent implements OnInit, OnDestroy {

  newsId:number;
  // news:NouvellesModel={id:-1, titre:'',description:'',img:''};
  news:NouvellesModel[];
  newsActu:NouvellesModel;
  filtre:string; // Filtrage des donnes de la liste des articles
  donnees$:Subscription; // Rcupration des donnes des Nouvelles
  
  // Ajout d'un diteur
  public editeur;
  public editeurContent = `<h3>I am Example content</h3>`;
  public editeurOptions = {
    placeholder: "insert content..."
  };

  /**
   * 
   * @param donnees Pointeur vers le service de donnes pour accder  la liste des news charges
   * @param routeParams Instancier le router pour rcuprer les id ventuels
   */
  constructor(public donnees:AnewsService, private routeParams:ActivatedRoute) {}

  /**
   * Initialisation du composant
   * On dfinit la news  diter en rcuprant le paramtre ID de la route
   * Si aucun paramtre n'est dfini, on affiche la premire route
   */
  ngOnInit() {
    this.newsId = 0; // Paramtrer une valeur par dfaut  l'ID
    this.routeParams.params.subscribe(params => {
      this.newsId = +params['id'];
      this.news = this.donnees.news;
      this.newsActu = this.donnees.getNews(this.newsId); // (+) converts string 'id' to a number
      // Rcuprer les donnes du service
      this.donnees$ = this.donnees.news$.subscribe(
        data => {
          this.news = data;
            console.log(this.news);
          if(this.newsId){
            // Identifier la nouvelle qui nous intresse
            this.newsActu = this.news[this.newsId];
          }else{
            this.newsActu = this.news[0];
          }
      });
    });
    console.log("dition charge");
  }
  
  /**
   * Fonction appele lors de la soumission du formulaire
   * @param f Donnes saisies dans le formulaire
   */
  edition(f:NgForm){
    console.log(f.value);
  }
  /**
   * Slectionner une news
   * @param e ID de la news  afficher (index du tableau des news)
   */
  setNewsActu(e:any){
    this.newsActu = this.news[e.target.value];
    console.log(this.newsActu);
  }
  /**
   * Mettre fin aux souscriptions des observables pour viter la surcharge de la mmoire
   */
  ngOnDestroy(){
    this.donnees$.unsubscribe();
  }
}
